import React, { Suspense, useEffect, useLayoutEffect } from "react";
import { Navigate, Outlet, Route, Routes } from "react-router-dom";
import CryptoJS from "crypto-js";
import NavBar from "./components/common/NavBar";
import "./App.css";
import logo from "./assets/LOGO1.png";
import { useDispatch, useSelector } from "react-redux";
import { ToastContainer } from "react-toastify";
import {
  createWeb3,
  getBalance,
  getWalletInfo,
} from "./components/utils/wallet";
import { ROUTES, RPC_SERVER } from "./components/constants/appConstants";
import { setWeb3, setWalletInfo } from "./components/store/slices/dappSlice";
import {
  getNetwork,
  getWeb3Info,
  removeWeb3Info,
} from "./components/utils/storage";
import _ from "lodash";
import Login from "./components/pages/Login/Login";
import Transaction from "./components/pages/Transaction/Transaction";
import { isElectron, removeRPFile } from "./components/utils/electron";

const Home = React.lazy(() => import("./components/pages/Home/Home"));
const WalletAccess = React.lazy(() =>
  import("./components/pages/WalletAccess/WalletAccess")
);
const MyTokens = React.lazy(() =>
  import("./components/pages/MyTokens/MyTokens")
);
const PageNotFound = React.lazy(() =>
  import("./components/pages/PageNotFound")
);
const arr = ["/create_wallet", "/import_wallet", "/offline_transaction"];

function PrivateRoute() {
  const auth = useAuth();
  return auth ? <Outlet /> : <Navigate to="/" />;
}
function PublicRoute() {
  const dispatch = useDispatch();
  useLayoutEffect(() => {
    sessionStorage.removeItem("address");
    sessionStorage.removeItem("privateKey");
    dispatch(setWalletInfo());
    removeWeb3Info();
    isElectron() && removeRPFile();
  }, []);
  return <Outlet />;
}
function useAuth() {
  const address = sessionStorage.getItem("address");
  return !!address;
}
function App() {
  const { loading } = useSelector(({ dapp }) => dapp);
  const dispatch = useDispatch();

  useEffect(() => {
    window.scrollTo(0, 0);
    const { address, hdPath, recoveryPhrase } = getWeb3Info() || {};
    const server = _.get(RPC_SERVER, [getNetwork()], "");
    const hdPaths = server.hdPath;
    const decryptedRP = sessionStorage.getItem("privateKey");
    const pKey =
      decryptedRP &&
      CryptoJS.AES.decrypt(decryptedRP, "mKzwETNaKl").toString(
        CryptoJS.enc.Utf8
      );
    const serverConfig = hdPaths
      ? {
          ...server,
          hdPath,
        }
      : server;

    if (recoveryPhrase) {
      const accessKey = pKey || recoveryPhrase;
      const newWeb3 = createWeb3(accessKey, serverConfig);

      dispatch(setWeb3(newWeb3));
      getWalletInfo(newWeb3).then((wallet) => {
        dispatch(setWalletInfo(wallet));
      });
    } else if (address) {
      getBalance(address, serverConfig).then((balance) => {
        const walletInfo = {
          address,
          balance,
        };
        sessionStorage.setItem("address", address);
        dispatch(setWalletInfo(walletInfo));
      });
    }
  }, []);

  return (
    <>
      <Suspense
        fallback={
          <div className="h-screen flex justify-center items-center">
            <img src={logo} alt="logo" className="animate-bounce" />
          </div>
        }
      >
        {loading && (
          <div className="fixed h-screen z-50 flex flex-col gap-4 bg-gray-400 fil bg-opacity-70 justify-center w-full items-center">
            <img src={logo} alt="loading" className="animate-bounce" />
          </div>
        )}
        <NavBar />
        <Routes>
          {/* PUBLIC ROUTES */}

          <Route element={<PublicRoute />}>
            <Route path="/" element={<Home />} />
          </Route>
          <Route element={<PublicRoute />}>
            <Route path={"/wallet_access/:loginType"} element={<Login />} />
          </Route>
          <Route element={<PublicRoute />}>
            <Route path="/wallet_access" element={<WalletAccess />} />
          </Route>
          <Route element={<PublicRoute />}>
            <Route path="*" element={<PageNotFound />} />
          </Route>

          {/* PRIVATE ROUTES */}

          <Route element={<PrivateRoute />}>
            <Route path="/mytokens" element={<MyTokens />} />
          </Route>
          <Route element={<PrivateRoute />}>
            <Route path={`${ROUTES.TRANSACTION}`} element={<Transaction />} />
          </Route>
        </Routes>
      </Suspense>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop
        closeOnClick
        theme="light"
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </>
  );
}

export default App;
