import React, { useContext } from "react";
import { useTranslation } from "react-i18next";
import { TiTick } from "react-icons/ti";
import AppContext from "../../../../context/context";
const Stepper = () => {
  const { t } = useTranslation();
  const { activeStep, stepDone } = useContext(AppContext);
  return (
    <div className="px-3 flex items-center justify-between text-base text-gray-600 dark:text-gray-400">
      <div className="flex flex-col sm:flex-row items-center">
        <div
          className={`${
            stepDone.includes(1) || activeStep !== 1
              ? "bg-green-400"
              : "bg-indigo-400"
          } w-8 h-8 sm:w-12 sm:h-12  rounded-full flex items-center justify-center text-white text-lg sm:text-xl`}
        >
          {stepDone.includes(1) || [2, 3].includes(activeStep) ? (
            <TiTick size={62} />
          ) : (
            <span>1</span>
          )}
        </div>
        <span className="hidden sm:block text-white ml-4 text-sm sm:font-medium">
          {t("Select File")}
        </span>
      </div>
      <div className="h-1 w-16 bg-blue-200 dark:bg-blue-600"></div>
      <div className="flex flex-col sm:flex-row items-center">
        <div
          className={`${
            activeStep === 2
              ? "bg-indigo-400"
              : activeStep === 3
              ? "bg-green-400"
              : "bg-gray-400"
          } w-8 h-8 sm:w-12 sm:h-12 rounded-full flex items-center justify-center text-white text-lg sm:text-xl`}
        >
          {activeStep !== 3 ? (
            <span className={activeStep !== 2 ? `text-gray-300` : `text-white`}>
              2
            </span>
          ) : (
            <TiTick size={62} />
          )}
        </div>
        <span
          className={`${
            activeStep < 2 ? "text-gray-300" : "text-white"
          } hidden sm:block ml-4 text-sm sm:font-medium`}
        >
          {t("Enter Password")}
        </span>
      </div>
    </div>
  );
};

export default Stepper;
