import React, { useCallback, useEffect, useState } from "react";
import { IoChevronDown } from "react-icons/io5";
import { LANGUAGE } from "../constants/appConstants";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { setSelectedLang } from "../store/slices/dappSlice";

const LanguageDropdown = () => {
  const { t } = useTranslation();
  const { i18n } = useTranslation();
  const [active, setActive] = useState(false);
  const { selectedLang } = useSelector(({ dapp }) => dapp);
  const dispatch = useDispatch();
  const [languagesList, setLanguagesList] = useState([
    {
      label: "ENGLISH",
      disabled: false,
      inactive: false,
      name: LANGUAGE.english,
      clickHandler: () => changeLang(LANGUAGE.english),
    },
    {
      label: "FRANÇAIS",
      disabled: false,
      inactive: false,
      name: LANGUAGE.french,
      clickHandler: () => changeLang(LANGUAGE.french),
    },
    {
      label: "عربى",
      disabled: false,
      inactive: false,
      name: LANGUAGE.arabic,
      clickHandler: () => changeLang(LANGUAGE.arabic),
    },
    {
      label: "РУССКИЙ",
      disabled: false,
      inactive: false,
      name: LANGUAGE.russian,
      clickHandler: () => changeLang(LANGUAGE.russian),
    },
  ]);

  const setSelectedLanguage = useCallback(
    (language) => {
      let data;
      languagesList.forEach((lang) => {
        if (lang.name === language) data = lang;
      });
      data
        ? dispatch(setSelectedLang(data))
        : dispatch(setSelectedLang(languagesList[0]));
    },
    [languagesList]
  );

  const changeLang = useCallback(
    (language) => {
      i18n.changeLanguage(language);
      language &&
        setLanguagesList(() =>
          languagesList?.map((lang) =>
            lang.name === language
              ? { ...lang, disabled: true, inactive: true }
              : { ...lang, disabled: false, inactive: false }
          )
        );
      language && setSelectedLanguage(language);
    },
    [i18n, setLanguagesList, languagesList, setSelectedLanguage]
  );

  useEffect(() => {
    const language = localStorage.getItem("i18nextLng");
    language &&
      setLanguagesList(() =>
        languagesList?.map((lang) =>
          lang.name === language
            ? { ...lang, disabled: true, inactive: true }
            : { ...lang, disabled: false, inactive: false }
        )
      );
    language
      ? setSelectedLanguage(language)
      : setSelectedLanguage(LANGUAGE.english);
  }, []);

  return (
    <div className="relative group z-40">
      <label
        className="cursor-pointer flex items-center gap-1"
        onClick={() => setActive(true)}
      >
        {t(selectedLang.label)} <IoChevronDown />
      </label>
      <ul
        className={`absolute rounded-xl border border-themeg2 flex invisible group-hover:visible flex-col gap-2 bg-themeg1 p-2 `}
      >
        {languagesList?.map((lang) => (
          <li
            className="py-2 px-1 cursor-pointer"
            key={lang.label}
            disabled={lang.disabled}
            // inactive={lang.inactive}
            onClick={() => {
              lang.clickHandler();
              setActive(false);
            }}
          >
            {lang.label}
          </li>
        ))}

        {/* <li
          className="py-2 px-1 cursor-pointer"
          onClick={() => setLang("Arabic")}
        >
          Arabic
        </li>
        <li
          className="py-2 px-1 cursor-pointer"
          onClick={() => setLang("French")}
        >
          French
        </li>
        <li
          className="py-2 px-1 cursor-pointer"
          onClick={() => setLang("Russian")}
        >
          Russian
        </li> */}
      </ul>
    </div>
  );
};

export default LanguageDropdown;
