import React from "react";
import { useTranslation } from "react-i18next";
import * as Yup from "yup";
import { Formik } from "formik";
import { isPrivateKey } from "../../../utils/utilities";
import { HiEye, HiEyeOff } from "react-icons/hi";

const PrivateKey = ({ accessWallet }) => {
  const { t } = useTranslation();
  const [eye, setEye] = React.useState(false);

  const validationSchema = Yup.object().shape({
    privateKey: Yup.string()
      .test(
        "isValidPrivateKey",
        "Invalid Ethereum private key",
        async (value) => {
          if (!value) return false;
          try {
            const validKey = isPrivateKey(value);
            return validKey;
          } catch (error) {
            return false;
          }
        }
      )
      .required("Required"),
    acceptTerms: Yup.bool()
      .oneOf([true], "You must accept the terms to proceed")
      .required(),
  });

  const handleSubmit = (values) => {
    const { privateKey, acceptTerms } = values;

    if (acceptTerms) accessWallet("", privateKey);
  };

  return (
    <Formik
      initialValues={{
        privateKey: "",
        acceptTerms: false,
      }}
      validationSchema={validationSchema}
      onSubmit={(values) => handleSubmit(values)}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
      }) => (
        <form
          onSubmit={handleSubmit}
          autoComplete="off"
          className="flex flex-col gap-8 w-full"
        >
          <div className="flex flex-col gap-3 w-full">
            <h1 className="text-white lg:text-2xl text-center text-xl font-bold">
              {t("Enter your Private Key")}
            </h1>
            <div className="w-full flex items-center gap-4">
              <input
                className="w-full boxshadowinset border border-themeg2 rounded-xl appearance-none text-lg p-3 sm:p-5 outline-none placeholder:text-themepurple"
                type={eye ? "text" : "password"}
                placeholder={`${t("Type a 64-character length hex string")}`}
                name="privateKey"
                id="privateKey"
                onChange={handleChange}
                value={values.privateKey}
                onBlur={handleBlur}
              />
              {eye ? (
                <HiEye
                  className="w-4 h-4 sm:w-6 sm:h-6 cursor-pointer"
                  onClick={() => setEye(false)}
                />
              ) : (
                <HiEyeOff
                  className="w-4 h-4 sm:w-6 sm:h-6 cursor-pointer"
                  onClick={() => setEye(true)}
                />
              )}
            </div>
            {errors.privateKey && touched.privateKey && (
              <span className="text-xs text-red-600 font-semibold">
                {errors.privateKey}
              </span>
            )}
          </div>
          <div className="flex gap-2 justify-center">
            <input
              type="checkbox"
              name="acceptTerms"
              id="acceptTerms"
              onChange={handleChange}
              checked={values.acceptTerms}
              className="form-checkbox cursor-pointer h-5 w-5 text-blue-500"
            />
            <label
              className="block text-white font-bold cursor-pointer"
              htmlFor="acceptTerms"
            >
              {t("To access my wallet, I accept Terms")}
            </label>
          </div>
          <div className="flex lg:flex-row flex-col justify-center gap-4">
            <button
              type="submit"
              className="text-white py-4 px-16 border outline-none font-medium rounded-xl"
            >
              {t("Access Wallet")}
            </button>
          </div>
        </form>
      )}
    </Formik>
  );
};

export default PrivateKey;
