import React from "react";
import CryptoJS from "crypto-js";
import {
  encryptKeystore,
  isPrivateKey,
  isRecoveryPhrase,
} from "../../utils/utilities";
import _ from "lodash";
import {
  KEY_INPUT_TYPE,
  LOGIN_TYPE,
  ROUTES,
  RPC_SERVER,
} from "../../constants/appConstants";
import { getNetwork, setWeb3Info } from "../../utils/storage";
import {
  createWeb3,
  getWalletInfo,
  mnemonicToPrivateKey,
} from "../../utils/wallet";
import { toast } from "react-toastify";
import {
  setPrivateKey,
  setWalletInfo,
  setWeb3,
} from "../../store/slices/dappSlice";
import { isElectron, removeKeystore, writeRPFile } from "../../utils/electron";
import { useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import Heading from "./Components/Heading";
import PrivateKey from "./Components/PrivateKey";
import Mnemonic from "./Components/Mnemonic";
import Keystore from "./Components/Keystore";
import Warning from "./Components/Warning";

const Login = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const { loginType } = useParams();
  const handleAccessByRecoveryPhrase = (
    accessType = "",
    privateKey = "",
    recoveryPhrase = "",
    keyInputType = 1
  ) => {
    let errorList = {};
    // Validate form inputs
    if (keyInputType === KEY_INPUT_TYPE.PRIVATE_KEY) {
      errorList = {
        ...(!isPrivateKey(privateKey)
          ? {
              privateKey: ["Invalid Private Key"],
            }
          : {}),
      };
    } else if (keyInputType === KEY_INPUT_TYPE.RECOVERY_PHRASE) {
      errorList = {
        ...(!isRecoveryPhrase(recoveryPhrase)
          ? {
              recoveryPhrase: ["Invalid Recovery Phrase"],
            }
          : {}),
      };
    }

    if (_.isEmpty(errorList)) {
      // Create Web3 provider & store wallet data into state if form validation is passed.
      try {
        const accessKey = recoveryPhrase || privateKey;
        const server = RPC_SERVER[getNetwork()];
        const hdPath = server.hdPath;
        const updatedRpcServer = hdPath
          ? {
              ...server,
              hdPath,
            }
          : server;
        const newWeb3 = createWeb3(accessKey, updatedRpcServer);
        dispatch(setWeb3(newWeb3));
        const privKey = recoveryPhrase
          ? mnemonicToPrivateKey(recoveryPhrase, server)
          : privateKey;
        dispatch(setPrivateKey(privKey));
        const encryptedRP = CryptoJS.AES.encrypt(
          privKey,
          "mKzwETNaKl"
        ).toString();
        sessionStorage.setItem("privateKey", encryptedRP);
        getWalletInfo(newWeb3)
          .then((walletInfo) => {
            dispatch(setWalletInfo(walletInfo));
            setWeb3Info({
              loginType: LOGIN_TYPE.PRIVATE_KEY,
              recoveryPhrase: accessKey,
              address: sessionStorage.getItem("address"),
              ...(hdPath
                ? {
                    hdPath,
                  }
                : {}),
            });
          })
          .then(() => {
            if (isElectron() && accessType !== "keystore") {
              // Specific handle in Electron app:
              // Store encrypted wallet key into temporary file for quick access
              const privKey = recoveryPhrase
                ? mnemonicToPrivateKey(recoveryPhrase, server)
                : privateKey;
              dispatch(setPrivateKey(privKey));
              const encryptedRP = CryptoJS.AES.encrypt(
                privKey,
                "mKzwETNaKl"
              ).toString();
              sessionStorage.setItem("privateKey", encryptedRP);
              removeKeystore().then(
                ({ error }) => error && toast.error(error.message)
              );
              writeRPFile(
                JSON.stringify(encryptKeystore(privKey, "recoveryPhrase"))
              ).then(({ error }) => error && toast.error(error.message));
            }
            navigate("/mytokens");
          });
      } catch (error) {
        console.error("Error", error.message);
      }
    } else {
      console.error("Error", errorList);
    }
  };

  return (
    <div className="h-full bg-themepurple px-5 py-8 sm:py-16 flex items-center flex-col gap-10">
      <div className="flex flex-col gap-5 ">
        <Heading loginType={loginType} />
        <div className="flex flex-col gap-4">
          <div className="flex flex-col just-start gap-4 items-start">
            <div className="w-full flex items-center gap-4 relative container">
              {ROUTES.PRIVATE_KEY.includes(loginType) && (
                <PrivateKey accessWallet={handleAccessByRecoveryPhrase} />
              )}
              {ROUTES.MNEMONIC.includes(loginType) && (
                <Mnemonic accessWallet={handleAccessByRecoveryPhrase} />
              )}
              {ROUTES.KEY_STORE.includes(loginType) && (
                <Keystore accessWallet={handleAccessByRecoveryPhrase} />
              )}
            </div>
          </div>
        </div>
        <Warning />
      </div>
    </div>
  );
};

export default Login;
