import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";
import getTranslations from "./translations";
const { arabicJson, englishJson, russianJson, frenchJson } = getTranslations();

i18n
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    resources: {
      english: { translation: englishJson },
      french: { translation: frenchJson },
      russian: { translation: russianJson },
      arabic: { translation: arabicJson },
    },
    fallbackLng: "english",
    debug: false,
    react: {
      useSuspense: false,
    },
  });

export default i18n;
