import { useTranslation } from "react-i18next";
import { ROUTES } from "../../../constants/appConstants";
import PropTypes from "prop-types";

const Heading = ({ loginType }) => {
  const { t } = useTranslation();

  return (
    <>
      {ROUTES.MNEMONIC.includes(loginType) && (
        <div className="text-2xl sm:text-5xl font-bold text-white text-center">
          {t("Access Wallet with Mnemonic Phrase")}
        </div>
      )}
      {ROUTES.KEY_STORE.includes(loginType) && (
        <div className="text-2xl sm:text-5xl font-bold text-white text-center">
          {t("Access Wallet with Keystore File")}
        </div>
      )}
      {ROUTES.PRIVATE_KEY.includes(loginType) && (
        <div className="text-2xl sm:text-5xl font-bold text-white text-center">
          {t("Access Wallet with Private Key")}
        </div>
      )}
    </>
  );
};

Heading.propTypes = {
  loginType: PropTypes.string.isRequired,
};
export default Heading;
