import React, { useEffect, useRef, useState } from "react";
import { GiHamburgerMenu } from "react-icons/gi";
import logo from "../../assets/LOGO1.png";
import LanguageDropdown from "../ReusableComponent/LanguageDropdown";
import SideBarMenu from "../pages/Layout/components/SideBarMenu";

const NavBar = () => {
  const [showSideBar, setShowSideBar] = useState(false);
  const sidebarRef = useRef(null);

  const handleClickOutside = (event) => {
    if (sidebarRef.current && !sidebarRef.current.contains(event.target)) {
      setShowSideBar(false);
    }
  };
  useEffect(() => {
    document.addEventListener("click", handleClickOutside);

    return () => {
      document.removeEventListener("click", handleClickOutside);
    };
  }, []);
  return (
    <>
    <div className="block sm:hidden flex justify-between items-center gap-3 h-fit">
      <span className="w-auto px-4 py-6  text-center" ref={sidebarRef}>
        <GiHamburgerMenu
          size={25}
          onClick={() => {
            setShowSideBar((prevState) => !prevState);
          }}
        />
        {showSideBar && <SideBarMenu setShowSideBar={setShowSideBar} />}
      </span>
      <img src={logo} alt="logo" className="w-16 h-16" />
    </div>
    <div className="hidden sm:flex 2xl:px-24 md:px-16 px-8 pt-2 pb-2 flex justify-between items-center gap-8 bg-themeg1 left-0 w-full">
      <img src={logo} alt="logo" className="w-20" />
      <LanguageDropdown />
    </div>
    </>
  );
};

export default NavBar;
