import React from "react";
import CryptoJS from "crypto-js";
import address from "../../../../assets/icons/address.png";
import arrow from "../../../../assets/arrow.png";
import paper from "../../../../assets/icons/paper.png";
import copy from "../../../../assets/icons/copy.png";
import print from "../../../../assets/icons/print.png";
import gifttext from "../../../../assets/icons/gift.png";
import gift from "../../../../assets/icons/gift-text.png";
import cut from "../../../../assets/icons/cut.png";
import logo from "../../../../assets/LOGO.png";
import Modal from "../../../common/Modal";
import QRCode from "react-qr-code";
import { useTranslation } from "react-i18next";
import { IoIosLogOut } from "react-icons/io";

import { copyFunction } from "../../../common/common";
import _ from "lodash";
import { removeWeb3Info } from "../../../utils/storage";
import { useDispatch } from "react-redux";
import { setWalletInfo } from "../../../store/slices/dappSlice";
import { isElectron, removeRPFile } from "../../../utils/electron";
import { useNavigate } from "react-router-dom";
import LanguageDropdown from "../../../ReusableComponent/LanguageDropdown";

const Portfolio = ({ openToast, usdAmount, tokens, seZrcToken }) => {
  const { t } = useTranslation();
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [open, setOpen] = React.useState(false);
  const [open1, setOpen1] = React.useState(false);
  const [isMetamask, setIsMetamask] = React.useState(false);

  const walletAddress = sessionStorage.getItem("address");
  const decryptedRP = sessionStorage.getItem("privateKey");
  const privateKey =
    decryptedRP &&
    CryptoJS.AES.decrypt(decryptedRP, "mKzwETNaKl").toString(CryptoJS.enc.Utf8);

  const handleOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  const handleOpen1 = () => {
    setOpen1(true);
  };
  const handleClose1 = () => {
    setOpen1(false);
  };

  const printDiv = () => {
    let divContent = document.getElementById("print").innerHTML;
    let a = window.open();
    a.document.write("<html>");
    a.document.write("<head>");
    a.document.write(
      "<link href='https://unpkg.com/tailwindcss@^2/dist/tailwind.min.css' rel='stylesheet'>"
    );
    a.document.write("</head>");
    a.document.write("<body>");
    a.document.write(divContent);
    a.document.write("</body>");
    a.document.write("</html>");
    a.document.close();
    a.print();
  };

  const handleLogout = () => {
    sessionStorage.removeItem("balance");
    sessionStorage.removeItem("address");
    sessionStorage.removeItem("privateKey");
    Promise.all([
      dispatch(setWalletInfo()),
      removeWeb3Info(),

      isElectron() && removeRPFile(),
    ]).then(() => navigate("/"));
  };

  return (
    <>
      <div className="flex flex-col gap-2 2xl:pl-4 lg:pl-2 pl-0 lg:pt-0 pt-2 w-full">
        <h1 className="2xl:text-2xl text-xl font-medium whitespace-nowrap text-justify">
          {t("Portfolio")}
        </h1>
        <div className="flex flex-col items-center w-full gap-4">
          <div className="w-full flex flex-col gap-4 justify-between">
            <p className="truncate text-ellipsis font-medium w-28 ">
              {walletAddress}
            </p>
            <div className="grid grid-cols-1 2xl:gap-10 gap-4">
              <div
                className="flex justify-start items-center flex-row gap-2 cursor-pointer whitespace-nowrap"
                onClick={handleOpen}
              >
                <img
                  src={address}
                  alt="address"
                  className="2xl:w-6 w-4 2xl:h-6 h-4"
                />
                <p className="text-sm text-center">{t("Address QR Code")}</p>
              </div>
              <div
                className="flex justify-start items-center flex-row gap-2 cursor-pointer whitespace-nowrap"
                onClick={handleOpen1}
              >
                <img
                  src={paper}
                  alt="paper"
                  className="2xl:w-6 w-4 2xl:h-6 h-4"
                />
                <p className="text-sm text-center">{t("Paper Wallet")}</p>
              </div>
              <div
                className="flex justify-start items-center flex-row gap-2 cursor-pointer whitespace-nowrap"
                onClick={() => copyFunction(walletAddress)}
              >
                <img
                  src={copy}
                  alt="copy"
                  className="2xl:w-6 w-4 2xl:h-6 h-4"
                />
                <p className="text-sm text-center">
                  {t("Copy Wallet Address")}
                </p>
              </div>
              <div className="text-sm sm:font-base sm:hidden">
                <LanguageDropdown />
              </div>
              <button
                className="flex justify-start items-center flex-row gap-2 cursor-pointer whitespace-nowrap text-sm sm:font-base"
                onClick={handleLogout}
              >
                <img
                  src={arrow}
                  alt="copy"
                  className="2xl:w-6 w-4 2xl:h-6 h-4"
                />
                {/* <IoIosLogOut width={20} className="font-bold" /> */}
                <h1>{t("Logout")}</h1>
              </button>
            </div>
          </div>
        </div>
      </div>
      <Modal open={open} close={handleClose}>
        <div className="p-8 flex flex-col gap-4 justify-between items-center text-sm w-full ">
          <div className="text-2xl font-bold">
            My public address to receive funds
          </div>
          <div>
            To receive ETH from another account, send ETH from that account to
            this address.
          </div>
          <div>
            <QRCode value={walletAddress || ""} size={200} />
          </div>
          <h1 className="font-medium">{walletAddress}</h1>
          <button
            className="flex items-center gap-2 cursor-pointer"
            onClick={() => {
              copyFunction(walletAddress);
              handleClose();
            }}
          >
            <img src={copy} alt="copy" className="2xl:w-6 w-4 2xl:h-6 h-4" />
            <p className="text-sm">Copy Address</p>
          </button>
        </div>
      </Modal>
      <Modal open={open1} close={handleClose1}>
        <div className="p-4 flex flex-col xl:gap-4 gap-2" id="print">
          <div className="flex flex-col xl:gap-4 gap-2">
            <div className="md:px-6 px-0 flex flex-col xl:gap-4 gap-2">
              <div className="w-full flex justify-center pb-4">
                <img
                  src={logo}
                  alt="logo"
                  className="xl:w-60 lg:w-52 w-60 xl:h-30 lg:h-24 h-30"
                />
              </div>
              <div className="border-2 border-themeblue rounded-xl md:p-3 p-2 flex items-center gap-8">
                <div className="bg-white">
                  <QRCode
                    value={walletAddress || ""}
                    style={{ background: "white" }}
                    size={100}
                  />
                </div>
                <div>
                  <p className="2xl:text-2xl lg:text-xl text-lg font-medium break-words xl:w-full lg:w-4/5 md:w-full sm:w-1/2 font-bold">
                    Address
                  </p>
                  <p className="md:text-base text-sm font-medium break-all">
                    {walletAddress}
                  </p>
                </div>
              </div>
              <div className="border-2 border-themeblue rounded-xl md:p-3 p-2 flex items-center gap-8">
                <div className="bg-white">
                  <QRCode
                    value={privateKey || ""}
                    style={{ background: "white" }}
                    size={100}
                  />
                </div>
                <div>
                  <p className="2xl:text-2xl lg:text-xl text-lg font-medium break-words xl:w-full lg:w-4/5 md:w-full sm:w-1/2 font-bold">
                    My Private Key
                  </p>
                  <p className="md:text-base text-sm font-medium break-all">
                    {privateKey}
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div className="">
            <div className="md:px-6 px-0 py-4 flex flex-col xl:gap-4 gap-2">
              <div className="flex justify-between items-center cursor-pointer">
                <img src={logo} alt="logo" className="w-32 h-14" />
                <div className="flex gap-2 items-center" onClick={printDiv}>
                  <img src={print} alt="print" />
                  <h1 className="underline">Print</h1>
                </div>
              </div>
            </div>
          </div>
        </div>
      </Modal>
    </>
  );
};

export default Portfolio;
