export const trimMnemonic = (rawMnemonic) => {
  if (rawMnemonic) {
    const words = rawMnemonic
      .trim() // Remove beginning & end spaces
      .replace(/[\r\n]+/g, "") // Remove break-line characters
      .split(/[ ]+/);
    return words.join(" ");
  }
  return "";
};

