import React from "react";
import { useTranslation } from "react-i18next";
import * as Yup from "yup";
import { Formik } from "formik";

const Mnemonic = ({ accessWallet }) => {
  const { t } = useTranslation();

  const validationSchema = Yup.object().shape(
    {
      mnemonicPhrase: Yup.string().when("privateKey", {
        is: (privateKey) => !privateKey,
        then: Yup.string().required("Required"),
      }),
    },
    ["mnemonicPhrase"]
  );

  const handleSubmit = (values) => {
    const rPhrase = values.mnemonicPhrase;

    accessWallet("", "", rPhrase, 2);
  };

  return (
    <Formik
      initialValues={{
        mnemonicPhrase: "",
      }}
      validationSchema={validationSchema}
      onSubmit={(values) => handleSubmit(values)}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
      }) => (
        <form
          onSubmit={handleSubmit}
          autoComplete="off"
          className="flex flex-col gap-3 w-full"
        >
          <div>
            <div className="flex flex-col gap-3 w-full">
              <h1 className="text-white lg:text-2xl text-xl font-bold">
                {t("Mnemonic Phrase")}
              </h1>
              <p className="text-white lg:text-base text-sm font-medium">
                {t("Your recovery phrase is also the key to the wallet")}
              </p>
              <textarea
                className="boxshadowinset border border-themeg2 rounded-xl appearance-none text-lg p-3 sm:p-5 outline-none placeholder:text-themepurple lg:placeholder:text-lg placeholder:text-base"
                placeholder={`${t("Type a 12-word phrase")}`}
                name="mnemonicPhrase"
                id="mnemonicPhrase"
                onChange={handleChange}
                value={values.mnemonicPhrase}
                onBlur={handleBlur}
              />
              {errors.mnemonicPhrase && touched.mnemonicPhrase && (
                <span className="text-xs text-red-600 font-semibold">
                  {errors.mnemonicPhrase}
                </span>
              )}
            </div>
          </div>
          <div className="flex flex-col justify-center gap-4">
            <p className="text-white  text-sm font-medium">
              {t("Hit ‘SPACE’ after every successful word entry.")}
            </p>
            <div>
              <button
                type="submit"
                className="w-fit py-4 px-16  border outline-none text-white font-medium rounded-xl"
              >
                {t("Import")}
              </button>
            </div>
          </div>
        </form>
      )}
    </Formik>
  );
};

export default Mnemonic;
