import React, { useCallback, useEffect, useRef } from "react";
import Web3 from "web3";
import {
  LOGIN_TYPE,
  NETWORK_TYPE,
  RPC_SERVER,
  WEB3_STATUSES,
} from "../constants/appConstants";
import {
  getNetwork,
  getWeb3Info,
  removeWeb3Info,
  setNetwork,
  setWeb3Info,
} from "../utils/storage";
import { createWeb3, getWalletInfo } from "../utils/wallet";
import _ from "lodash";
import { useDispatch } from "react-redux";
import {
  setMetaFlag,
  setWalletInfo,
  setWeb3 as web3Instance,
} from "../store/slices/dappSlice";

export const Web3Context = React.createContext({
  web3: {},
  web3Status: "",
  switchRPCServer: () => {},
});

const Web3Provider = ({ children }) => {
  const [web3, setWeb3] = React.useState({});
  const [status, setStatus] = React.useState(WEB3_STATUSES.LOADING);
  // const [error, setError] = React.useState(null);
  const [rpcServer, setRpcServer] = React.useState({});

  const dispatch = useDispatch();

  let newkey = useRef("");

  const checkMetaMaskLogin = useRef(null);

  const handleUpdateMetaMaskAccount = useCallback(() => {
    const newWeb3 = new Web3(Web3.givenProvider);

    return Web3.givenProvider._metamask
      .isUnlocked()
      .then((bool) => {
        if (bool) {
          const networkKey = getNetwork() || NETWORK_TYPE.WETHIOCHAIN_MAINNET;
          const rpcServer = _.get(RPC_SERVER, [networkKey]);
          setRpcServer(rpcServer);
          handleSetWeb3(newWeb3);
          return getWalletInfo(newWeb3);
        }
      })
      .then((walletInfo) => {
        if (walletInfo) {
          setWeb3Info({
            address: walletInfo.address,
            loginType: LOGIN_TYPE.META_MASK,
          });
          dispatch(setWalletInfo(walletInfo));
          return true;
        }
      });
  }, [dispatch]);

  const updateMetaMaskAccountListener = useCallback(() => {
    handleUpdateMetaMaskAccount();
  }, [handleUpdateMetaMaskAccount]);

  const handleUpdateRpcServer = useCallback((newKey) => {
    newkey.current = newKey;
    const val = _.get(RPC_SERVER, newKey, {});

    setRpcServer(val);
  }, []);

  const handleInitiateDefaultWeb3 = useCallback(() => {
    const networkKey = getNetwork();
    if (networkKey) {
      handleUpdateRpcServer(networkKey);
    } else {
      handleUpdateRpcServer(NETWORK_TYPE.WETHIOCHAIN_MAINNET);
    }
  }, [handleUpdateRpcServer]);

  const handleRemoveMetaMaskProvider = useCallback(() => {
    if (checkMetaMaskLogin.current) {
      clearInterval(checkMetaMaskLogin.current);
      checkMetaMaskLogin.current = null;
    }
    if (window.ethereum) {
      window.ethereum.removeListener(
        "accountsChanged",
        updateMetaMaskAccountListener
      );
    }
  }, [updateMetaMaskAccountListener]);

  const handleSetMetaMaskProvider = useCallback(async () => {
    if (Web3.givenProvider) {
      return new Promise(async (resolve, reject) => {
        const web3 = new Web3(Web3.givenProvider);
        const networkKey = getNetwork() || NETWORK_TYPE.WETHIOCHAIN_MAINNET;
        const rpcServer = _.get(RPC_SERVER, [networkKey]);
        const netId = await web3.eth.net.getId();

        if (netId === rpcServer?.networkId) {
          handleMetamaskConnect(reject, resolve);
        } else {
          try {
            await web3.currentProvider.request({
              method: "wallet_switchEthereumChain",
              params: [{ chainId: Web3.utils.toHex(rpcServer?.networkId) }],
            });
            handleMetamaskConnect(reject, resolve);
          } catch (switchError) {
            if (switchError.code === 4902) {
              try {
                await web3.currentProvider.request({
                  method: "wallet_addEthereumChain",
                  params: [
                    {
                      chainId: Web3.utils.toHex(rpcServer?.networkId),
                      chainName: chainName(rpcServer?.networkId),
                      rpcUrls: [rpcServer?.host] /* ... */,
                    },
                  ],
                });
                handleMetamaskConnect(reject, resolve);
              } catch (addError) {
                console.error("Error", addError);
              }
            }
          }
        }
      });
    }
  }, [
    dispatch,
    handleRemoveMetaMaskProvider,
    handleUpdateMetaMaskAccount,
    updateMetaMaskAccountListener,
  ]);

  const chainName = (id) => {
    if (id === 77) {
      return "Wethio Devnet";
    } else if (id === 79) {
      return "Wethio Testnet";
    } else {
      return "Wethio";
    }
  };

  const handleMetamaskConnect = (reject, resolve) => {
    window.ethereum
      .enable()
      .then(() => {
        return handleUpdateMetaMaskAccount();
      })
      .then(() => {
        window.ethereum.on("accountsChanged", updateMetaMaskAccountListener);
        if (!checkMetaMaskLogin.current) {
          checkMetaMaskLogin.current = setInterval(() => {
            Web3.givenProvider._metamask.isUnlocked().then((bool) => {
              const web3Info = getWeb3Info();
              if (
                !_.isEmpty(web3Info) &&
                web3Info.loginType === LOGIN_TYPE.META_MASK &&
                !bool
              ) {
                removeWeb3Info();
                dispatch(setWalletInfo(null));
                handleRemoveMetaMaskProvider();
                reject(false);
              } else {
                dispatch(setMetaFlag(true));
                resolve(true);
              }
            });
          }, 1000);
        }
      });
  };

  const handleSetWeb3 = (web3) => {
    setWeb3(web3);
    dispatch(web3Instance(web3));
  };

  // const handleTryProvider = (web3, next = null) => {
  //   if (web3) {
  //     handleSetWeb3(web3);
  //   } else if (next) {
  //     next();
  //   } else {
  //     setStatus(WEB3_STATUSES.FAILED);
  //     setError("Unexpected Web3 error!");
  //   }
  // };

  useEffect(() => {
    web3?.eth?.net
      .isListening()
      .then(() => setStatus(WEB3_STATUSES.INITIALIZED))
      .catch(() => setStatus(WEB3_STATUSES.FAILED));
  }, [web3?.eth?.net]);

  useEffect(() => {
    const getNet = JSON.parse(sessionStorage.getItem("global"));

    const newKey = newkey.current ? newkey.current : getNet?.network;
    setNetwork(newKey);
    if (getNet?.web3Info?.loginType === "ledger" || !getNet?.web3Info) {
      const newWeb3 = createWeb3(null, rpcServer);
      handleSetWeb3(newWeb3);
    }
  }, [rpcServer]);

  useEffect(() => {
    const web3Info = getWeb3Info();

    if (
      _.isEqual(_.get(web3Info, "loginType"), LOGIN_TYPE.META_MASK) &&
      Web3.givenProvider
    ) {
      handleSetMetaMaskProvider();
    } else if (_.get(web3Info, "recoveryPhrase")) {
      const { recoveryPhrase } = web3Info;
      const networkKey = getNetwork() || NETWORK_TYPE.WETHIOCHAIN_MAINNET;

      const rpcServer = _.get(web3Info, "hdPath")
        ? {
            ..._.get(RPC_SERVER, [networkKey]),
            hdPath: _.get(web3Info, "hdPath"),
          }
        : _.get(RPC_SERVER, [networkKey]);

      const newWeb3 = createWeb3(recoveryPhrase, rpcServer);

      handleSetWeb3(newWeb3);
      setRpcServer(rpcServer);
    } else {
      handleInitiateDefaultWeb3();
    }

    return () => {
      handleRemoveMetaMaskProvider();
    };
  }, []);

  return (
    <Web3Context.Provider
      value={{
        web3,
        web3Status: status,
        rpcServer,
        removeMetaMaskProvider: handleRemoveMetaMaskProvider,
        setMetaMaskProvider: handleSetMetaMaskProvider,
        switchRPCServer: handleUpdateRpcServer,
        updateWeb3: handleSetWeb3,
      }}
    >
      {children}
    </Web3Context.Provider>
  );
};

export default Web3Provider;
