import { useEffect } from "react";
import { toast } from "react-toastify";
import Web3 from "web3";
import { CONTRACT_ABI } from "../constants/appConstants";
import { isValidChain, isValidNonce } from "../utils/utilities";

export const copyFunction = (value) => {
  navigator.clipboard.writeText(value);
  toast.info("Copied successfully");
};

export const useOutsideClick = (ref, containerRef, callback) => {
  const handleClick = (e) => {
    if (ref.current && !ref.current.contains(e.target)) {
      callback();
    }
  };

  useEffect(() => {
    containerRef?.current &&
      containerRef?.current?.addEventListener("click", handleClick);
    containerRef?.current &&
      containerRef?.current?.addEventListener("keypress", handleClick);

    return () => {
      containerRef?.current &&
        containerRef?.current?.removeEventListener("click", handleClick);
    };
  });
};

export const convertToEther = (val) => {
  return Web3.utils.fromWei(val, "ether")?.toString();
};

export async function signTokenTransaction({
  privateKey,
  to,
  amount,
  gasPrice,
  tokenAddress,
  gasLimit,
  nonce,
  chainId,
}) {
  try {
    const webInstance = new Web3();
    let account = webInstance.eth.accounts.privateKeyToAccount(privateKey);
    let receiver = webInstance.utils.toChecksumAddress(to);
    const trnObj = {
      to: tokenAddress,
      gasPrice: gasPrice,
      gas: gasLimit,
      nonce: nonce,
      chainId: chainId,
    };
    trnObj.data = encodeData(receiver, tokenAddress, amount);
    let signedTxObject = await webInstance.eth.accounts.signTransaction(
      trnObj,
      account.privateKey
    );
    return { rawTransaction: signedTxObject?.rawTransaction, tx: trnObj };
  } catch (ex) {
    console.log("Error occuured ", ex);
    toast.error(ex.message);
  }
}

export async function signCurrencyTransaction({
  privateKey,
  to,
  amount,
  gasPrice,
  gasLimit,
  nonce,
  chainId,
}) {
  try {
    const webInstance = new Web3();
    let account = webInstance.eth.accounts.privateKeyToAccount(privateKey);
    let receiver = webInstance.utils.toChecksumAddress(to);
    const amountValue = webInstance.utils.toWei(amount, "ether");
    const trnObj = {
      to: receiver,
      gasPrice: gasPrice,
      gas: gasLimit,
      value: amountValue,
      nonce: nonce,
      chainId: chainId,
    };
    let signedTxObject = await webInstance.eth.accounts.signTransaction(
      trnObj,
      account.privateKey
    );
    return { rawTransaction: signedTxObject?.rawTransaction, tx: trnObj };
  } catch (ex) {
    console.log("Error occuured ", ex);
    toast.error(ex.message);
  }
}

export function encodeData(receiver, tokenAddress, amount) {
  if (amount) {
    const webInstance = new Web3();
    let tokenAmount = webInstance.utils.toWei(amount, "ether");
    let contractInstance = new webInstance.eth.Contract(
      CONTRACT_ABI,
      tokenAddress
    );
    return contractInstance.methods.transfer(receiver, tokenAmount).encodeABI();
  }
}

export const isValidJSON = (data) => {
  const webInstance = new Web3();
  const chainValidity = isValidChain(webInstance, data?.chainID);
  const nonceValidity = isValidNonce(webInstance, data?.nonce);
  const gasValidity = isValidNonce(webInstance, data.gasPrice);

  return chainValidity && nonceValidity && gasValidity;
};
