import React from "react";
import { exportData } from "../../../utils/utilities";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";

const Card = ({ rawTransaction, tx }) => {
  const { t } = useTranslation();
  const navigate = useNavigate();
  const handleExport = () => {
    const exportValues = {
      rawTransaction,
      tx,
    };
    exportData(
      exportValues,
      `Raw_Transaction_${new Date().getTime()}.json`,
      "text/csv;charset=utf-8;"
    );
  };
  const handleback = () => {
    navigate(-1);
  };
  return (
    <>
      <div className="flex flex-col items-start">
        <div className="text-start text-start text-sm text-white">
          {t("Raw Transaction")}
        </div>
        <div className="w-full flex break-all p-10 text-white border-4 rounded-xl">
          {rawTransaction}
        </div>
      </div>
      <div className="flex flex-col items-start">
        <div className="text-start text-sm text-white">
          {t("Transaction Object")}
        </div>
        <div className="w-full flex break-all p-10 text-white border-4 rounded-xl">
          {JSON.stringify(tx)}
        </div>
      </div>
      <div className="flex gap-5 justify-center">
        <button
          className="cursor-pointer w-min p-4 text-white font-bold text-xl border rounded-xl"
          onClick={handleback}
        >
          {t("Back")}
        </button>
        <button
          className="cursor-pointer w-min p-4 text-white font-bold text-xl border rounded-xl"
          onClick={handleExport}
        >
          {t("Download")}
        </button>
      </div>
    </>
  );
};

export default Card;
