import React, { useCallback, useRef } from "react";
import { useOutsideClick } from "./common";

const Modal = ({ open, close, children }) => {
  const ref = useRef();
  const containerRef = useRef();

  useOutsideClick(ref, containerRef, close);

  const escFunction = useCallback(
    (event) => {
      if (event.keyCode === 27) {
        close();
      }
    },
    [close]
  );

  React.useEffect(() => {
    document.addEventListener("keydown", escFunction);

    return () => {
      document.removeEventListener("keydown", escFunction);
    };
  }, []);

  return (
    <>
      {open && (
        <div
          ref={containerRef}
          className={`fixed flex justify-center items-center h-screen w-screen top-0 right-0 left-0 z-50 bg-themeblue bg-opacity-50`}
        >
          <div
            className="2xl:w-2/5 xl:w-1/2 lg:w-3/5 md:w-4/5 w-11/12 text-themeblue bg-white rounded-3xl"
            ref={ref}
          >
            {children}
          </div>
        </div>
      )}
    </>
  );
};

export default Modal;
