import { Formik } from "formik";
import React, { useContext, useEffect } from "react";
import { useTranslation } from "react-i18next";
import upload from "../../../../assets/upload.png";
import * as Yup from "yup";
import { decryptKeystore } from "../../../utils/utilities";
import PropTypes from "prop-types";
import { isElectron, writeKeystore } from "../../../utils/electron";
import { HiEye, HiEyeOff } from "react-icons/hi";
import { toast } from "react-toastify";
import Stepper from "./Stepper";
import AppContext from "../../../../context/context";

const Keystore = ({ accessWallet }) => {
  const { t } = useTranslation();
  const { setActiveStep, activeStep } = useContext(AppContext);
  const [files, setFiles] = React.useState([]);
  const [fileData, setFiledata] = React.useState(null);
  const [eye, setEye] = React.useState(false);

  const validationSchema = Yup.object().shape({
    password: Yup.string()
      .required("Required")
      .matches("^(?=.{8,})", "Must Contain 8 Characters"),
  });

  const handleOpen = () => {
    setActiveStep(2);
  };
  const handleBack = () => {
    setActiveStep(1);
  };
  useEffect(() => {
    return () => {
      setActiveStep(1);
    };
  }, []);

  const handleImg = (event) => {
    event.preventDefault();

    if (event.target.files[0]) {
      const selectedFile = event.target.files[0];
      setFiles([selectedFile]);
      const fileReader = new FileReader();
      fileReader.readAsText(event.target.files[0]);
      fileReader.onloadend = () => {
        setFiledata(fileReader.result);
      };
    }
  };

  const handleDrop = (event) => {
    event.preventDefault();
    if (event.dataTransfer.files) {
      const selectedFile = event.dataTransfer.files[0];
      setFiles([selectedFile]);
    }
  };

  const handleSubmitKeystore = (values) => {
    const password = values.password;
    try {
      const decryptedData = decryptKeystore(fileData, password);
      if (isElectron()) {
        writeKeystore(fileData).then(({ error }) => {
          if (error) {
            toast.error("Error Password:", error.message);
          }
        });
      }

      new Promise((r) => r(decryptedData.privateKey))
        .then((res) => {
          accessWallet("keystore", res);
        })
        .then(() => {
          setActiveStep(3);
        });
    } catch (e) {
      toast.error("Error:", e.message);
    }
  };

  return (
    <div className="w-full flex flex-col gap-3">
      <div className="w-full">
        <Stepper />
      </div>
      {activeStep === 1 && (
        <div>
          <h1 className="text-white text-xl sm:text-2xl font-bold">
            {t("Select your Keystore File")}
          </h1>
          <div className="flex flex-col gap-5">
            <p className="text-white text-lg sm:text-xl">
              {t("Drag | Drop a file or click to download")}
            </p>
            <div
              onDrop={handleDrop}
              className="rounded-xl border border-themeg2 relative h-64 flex justify-center items-center w-full text-center"
            >
              {files.length > 0 && (
                <div>
                  <h1 className="text-lg break-all px-4 font-semibold">
                    {files[0].name}
                  </h1>
                </div>
              )}
              {files.length <= 0 && (
                <img src={upload} alt="upload" className="w-1/5" />
              )}
              <input
                onChange={(event) => {
                  // setFieldValue("file", event.currentTarget.files[0]);
                  handleImg(event);
                }}
                className="absolute text-white top-0 w-full h-full cursor-pointer opacity-0"
                type="file"
                name="file"
              />
            </div>
            <div className="text-start">
              <button
                onClick={handleOpen}
                className={`py-4 px-16 w-full sm:w-auto rounded-xl ${
                  files.length > 0
                    ? "border border-themeg2 font-semibold text-white"
                    : " text-white font-medium border border-themeg2"
                }`}
              >
                {t("Import")}
              </button>
            </div>
          </div>
        </div>
      )}
      {activeStep === 2 && (
        <div className="flex flex-col gap-2 text-sm w-full items-center ">
          <div className="border-b border-gray-400 px-4 w-full">
            <h1 className="text-xl sm:text-2xl font-bold text-white">
              {t("Enter Password")}
            </h1>
          </div>
          <Formik
            initialValues={{
              password: "",
            }}
            validationSchema={validationSchema}
            onSubmit={(values) => handleSubmitKeystore(values)}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
            }) => (
              <form
                onSubmit={handleSubmit}
                autoComplete="off"
                className="sm:px-4 w-full flex flex-col gap-4"
              >
                <div className="flex items-center gap-4">
                  <input
                    type={eye ? "text" : "password"}
                    placeholder={`${t(
                      "Password must contain more than 8 characters"
                    )}`}
                    name="password"
                    id="password"
                    onChange={handleChange}
                    value={values.password}
                    onBlur={handleBlur}
                    className="boxshadowinset border w-11/12 border-themeg2  px-3 py-2 sm:px-5 sm:py-4 text-lg rounded-xl outline-none placeholder:text-themepurple"
                  />
                  {eye ? (
                    <HiEye
                      className="w-4 h-4 sm:w-6 sm:h-6 cursor-pointer"
                      onClick={() => setEye(false)}
                    />
                  ) : (
                    <HiEyeOff
                      className="w-4 h-4 sm:w-6 sm:h-6 cursor-pointer"
                      onClick={() => setEye(true)}
                    />
                  )}
                </div>
                {errors.password && touched.password && (
                  <span className="text-xs text-red-600 font-semibold">
                    {errors.password}
                  </span>
                )}
                <div className="flex justify-start items-center gap-4 sm:gap-8">
                  <button
                    type="button"
                    className="text-white font-medium py-4 2xl:px-16 px-8  border border-themeg2 rounded-xl"
                    onClick={handleBack}
                  >
                    {t("Back")}
                  </button>
                  <button
                    type="submit"
                    className="text-white font-medium py-4 2xl:px-16 px-8 rounded-xl border border-themeg2"
                  >
                    {t("Confirm")}
                  </button>
                </div>
              </form>
            )}
          </Formik>
        </div>
      )}
    </div>
  );
};

Keystore.propTypes = {
  accessWallet: PropTypes.func.isRequired,
};
export default Keystore;
